@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#generalBackground {
  padding-top: 4em;
}

.yellowBg,
.whiteBg {
  min-height: 60vh;
  margin: 0%;
}

.yellowBg {
  background-color: #fffbf0;
}

.whiteBg {
  background-color: white;
}

#navbar {
  background-color: #195028;
  height: 4.8em;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100vw;
}

.navbarLogoGroup {
  display: inline-flex;
  align-items: center;
  padding-left: 2%;
  width: 40%;
}

.navbarTextGroup {
  display: inline-flex;
  align-items: center;
}

.nav-link,
.nav-link-active {
  font-family: Poppins;
  font-size: 18px;
  color: white;
  margin-left: 0.5em;
  margin-right: 0.5em;
  text-decoration: none;
}

.nav-link:hover,
.nav-link-active {
  background-color: #aec359;
  border-radius: 20px;
  padding: 0.3em 0.8em;
}

#logo-image {
  width: 60px;
  height: 60px;
  float: left;
}

#logo-text {
  font-family: Poppins;
  font-size: 25px;
  color: white;
  margin-left: 3%;
}

.contactContainer {
  display: inline-flex;
  flex-direction: column;
}

.inputGroup {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
}

input,
textarea {
  border: 0.05em solid #bbb7b7;
  border-radius: 0.5em;
  padding: 0.15em 0.5em;
  margin: 0.25em 0.5em;
  border-color: #94af26;
  border-width: 0.2em;
  background-color: #fffbf0;
}

input {
  height: 1.6em;
}

label {
  margin: 0 0 0 0.5em;
  font-family: Poppins;
  color: #94af26;
  font-size: 16px;
}

.paragraphInput {
  height: 6em;
}

#emailInputGroup,
#msgInputGroup,
.contactUsRow1 {
  width: 100%;
}
#emailInput,
#msgInput {
  width: 91%;
}

#mission,
#history,
#members {
  min-height: 50vh;
}
#mission {
  background-color: #fffbf0;
}
#history {
  background-color: white;
}
#members {
  background-color: #fffbf0;
}

#title {
  text-align: left;
  margin: 2% 10% 0% 10%;
  color: #195028;
  font-size: 45px;
}

.aboutSubtitle {
  color: #94af26;
  text-align: left;
  margin: 2% 13% 0% 13%;
  font-size: 28px;
  font-weight: 300;
}

.aboutText {
  text-align: left;
  vertical-align: middle;
  margin: 2% 16% 0% 16%;
  color: #195028;
  font-size: 17px;
}

.aboutMemberTitle {
  color: #195028;
  text-align: left;
  margin: 2% 13% 0% 13%;
  font-size: 28px;
  font-weight: 300;
}

.aboutMemberAvatars {
  width: 75%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
 
  margin-left: 250px;
}

.aboutIndividualAvatar {
  flex: 1 1;
    width: 20%;
    min-width: 20%;
    max-width: 20%;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p,
h1,
h2,
h3 {
  font-family: Poppins;
}

#home {
  background-color: #fffbf0;
  height: 100vh;
}
#image {
  width: 100vw;
  height: 100vh;
  margin-top: 4.8em;
}
.we {
  color: #195028;
  font-size: 100px;
  vertical-align: middle;
  margin-top: 0;
}

#pastevents {
    background-color: #fffbf0;
    padding-bottom: 15%;
}
#years {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); 
  margin-left: 2%;
  font-family: 'Helvetica';
  font-style: oblique;
  font-weight: bold;
  font-size: 15pt;
  color:#AFC05A;
  margin-top: -6%;
