  font-size: 15pt;
  color:#294F2C;
  margin-left: 2%;
}
.timeline ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #195028;
}
   

  .timeline ul li div {
    position: relative;
    bottom: 0;
    width: 400px;
    padding: 15px;
  }
   
  .timeline ul li div::before {
    content: '';
    position: absolute;
    bottom: 12px;
    width: 0;
    height: 0;
  }
  
  .timeline ul li:nth-child(1) div {
    left: 45px;
  }
   
  .timeline ul li:nth-child(1) div::before {
    height: 2px;
	  width: 500px;
	  background: #195028;
    left: -10%;
  }
  .timeline ul li:nth-child(3) div {
    left: -439px;
  }
   
  .timeline ul li:nth-child(3) div::before {
    height: 2px;
	  width: 500px; 
	  background: #195028;
    right: -3%;
  }
  .timeline ul li:nth-child(2) div {
    left: 45px;
  }
   
  .timeline ul li:nth-child(2) div::before {
    height: 2px;
	  width: 500px;
	  background: #fffbf0;
    left: -8%;
  }
  .timeline ul li:nth-child(4) div {
    left: -439px;
  }
   
  .timeline ul li:nth-child(4) div::before {
    height: 2px;
	  width: 500px; 
	  background: #fffbf0;
    right: 2%;
  }
  .timeline ul li:nth-child(1) :after {
    content: '';
    position: absolute;
    left: -42px;
    bottom: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #195028;
  }

  .timeline ul li:nth-child(3) :after {
    content: '';
    position: absolute;
    right: -42px;
    bottom: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #195028;
  }

  .timeline ul li:nth-child(5) div {
    left: 45px;
  }
   
  .timeline ul li:nth-child(5) div::before {
    height: 2px;
	  width: 500px;
	  background: #195028;
