  }
  .timeline ul li:nth-child(6) div {
    left: 45px;
  }
   
  .timeline ul li:nth-child(6) div::before {
    height: 2px;
	  width: 500px;
	  background: #fffbf0;
    left: -8%;
  }
  .timeline ul li:nth-child(8) div {
    left: -439px;
  }
   
  .timeline ul li:nth-child(8) div::before {
    height: 2px;
	  width: 500px; 
	  background: #fffbf0;
    right: 2%;
  }
  .timeline ul li:nth-child(5) :after {
    content: '';
    position: absolute;
    left: -42px;
